import React, {Component} from "react";
import "./style.scss";

class Loading extends Component {

    constructor(props) {
        super(props);
    }


    render() {

        let {isShow, text} = this.props;

        return (
            <div className={isShow ? 'loading-container show' : 'loading-container'}>
                <div className="loading-marker"></div>
                <div className="loading-box">
                    <img width="50" height="42"
                         src="data:image/gif;base64,"
                         alt="loading"/>
                    <p>{text}</p>
                </div>
            </div>

        )
    }
}

export default Loading;


